import React, { useEffect, useState } from 'react';
import _ from 'lodash';
import { createCanvas, initCanvas } from '../../helper/sandCanvas';
import './SandTest2.scss';

interface Props {
	chars?: string[];
	particlesBackToOrigin: boolean;
	mode: string;
	fullscreen?: boolean;
	screenMode?: string;
	refreshTrigger?: number;
}

export const SandTest2 = (
	{
		chars, particlesBackToOrigin, mode, fullscreen, screenMode = 'fullscreen', refreshTrigger = 0,
	}: Props,
) => {
	const [canvasCreated, setCanvasCreated] = useState<boolean>(false); // Edit Msg

	useEffect(() => {
		if (chars && chars?.length) {
			const formattedChars = (chars ? _.reverse([...chars]) : []).join('|');
			const options: any = {
				chars: formattedChars,
				particlesBackToOrigin,
				mode,
				screenMode: fullscreen ? 'fullscreen' : screenMode,
			};
			if (!canvasCreated) {
				setCanvasCreated(true);
				createCanvas(window, options);
			} else {
				initCanvas(options);
			}
		}
	}, [chars, particlesBackToOrigin, mode, fullscreen, screenMode, refreshTrigger]);

	return (
		<div
			className="sand-test-2"
		>
			<div className="ip-slideshow-wrapper">
				<div className="ip-slideshow" />
			</div>
		</div>
	);
};

export default SandTest2;
