import React, { useRef, useEffect, useContext } from 'react';
import {
	Link, Element, Events, animateScroll as scroll, scrollSpy, scroller, Button,
} from 'react-scroll';
// import ScrollArea from 'react-scrollbar';
import PerfectScrollbar from 'react-perfect-scrollbar';

import 'react-perfect-scrollbar/dist/css/styles.css';
import './ScrollPlane.scss';

interface Props {
	children?: JSX.Element[] | JSX.Element | string;
	startAtBottom?: boolean;
}

export const ScrollPlane = ({ children, startAtBottom }: Props) => {
	const scrollBarRef = useRef(null);

	useEffect(() => {
		setTimeout(() => {
			const scrollbarEl = document.querySelector('.start-at-bottom');
			if (scrollbarEl) {
				scrollbarEl.scrollTop = 999999;
			}
		}, 2000);
	}, []);

	return (
		<div className="scrollplane">
			<PerfectScrollbar
				className={`scrollplane__scroller ${startAtBottom ? 'start-at-bottom' : ''}`}
				ref={scrollBarRef}
				options={{
					suppressScrollX: true,
				}}
			>
				<div className="scrollplane__content">
					{children}
				</div>
			</PerfectScrollbar>
		</div>
	);
};

export default ScrollPlane;
