					<p>據稱，他們迷了路，在困惑中走進了一條漁村，遇見了阿群。有說阿群是女人，亦有說是男人，留著一條長長的辮子。當商人向阿群探問這個地方時，他們被帶到一條由小石鋪成、圍繞著整個小島的小徑，途經平緩的丘陵和崎嶇的山脈，穿越河流和瀑布。這條小徑後來被稱為「群帶路」（粵語讀音近似「大路」或「裙帶路」），這條在蜿蜒地勢上的道路，從港灣對面看，像是島嶼的腰帶，就如綁在阿群腰上的衫帶一樣。</p>
