/* eslint-disable max-len */
import React, { useEffect, useState, useContext } from 'react';
import useInterval from 'use-interval';
import _ from 'lodash';
import {
	useHistory,
} from 'react-router-dom';
import ReactSlider from 'react-slider';
import moment from 'moment';
import { TransformWrapper, TransformComponent } from 'react-zoom-pan-pinch';
import {
	Link, Element, Events, animateScroll as scroll, scrollSpy, scroller,
} from 'react-scroll';
import SandTest2 from '../SandTest2/SandTest2';
import LoaderDots from '../LoaderDots/LoaderDots';
import About from '../About/About';
import ScrollPlane from '../ScrollPlane/ScrollPlane';
import Acknowledgements from '../Acknowledgements/Acknowledgements';
import Follow from '../Follow/Follow';
import { fetchQuestions, fetchMessages, submitMessages } from '../../api';

import './SandTable.scss';

interface Props {
	fullscreen?: boolean;
}


export const SandTable = ({ fullscreen }: Props) => {
	const history = useHistory();
	const [refreshTrigger, setRefreshTrigger] = useState<number>(0);
	const [inputMsg, setInputMsg] = useState<string>('');
	const [questions, setQuestions] = useState<any>([]);
	const [rawMsgs, setRawMsgs] = useState<any>([]);
	const [msgs, setMsgs] = useState<string[]>([]);
	const [groupedMsgs, setGroupedMsg] = useState<any>([]);
	const [particlesBackToOrigin, setParticlesBackToOrigin] = useState<boolean>(true); // Hidden options
	const [mode, setMode] = useState<string>(fullscreen ? 'uniform' : 'pattern');
	const [screenMode, setScreenMode] = useState<string>(fullscreen ? 'fullscreen' : 'halfscreen');
	const [isTyping, setIsTyping] = useState<boolean>(false);
	const [testCount, setTestCount] = useState<number>(0);
	// Sections

	const [sectionOpen, setSectionOpen] = useState<string>('');


	const checkWallQuery = () => {
		const queries = new URLSearchParams(window.location.search);
		const wall = queries.get('wall');
		if (wall) {
			setScreenMode('fullscreen');
			history.replace('/wall');
		}
	};

	const loadMsgs = () => {
		// Initialize Message
		fetchQuestions().then((res) => {
			setQuestions(res.data);
			console.log('fetchQuestions', res);
		}).catch((e) => {
			//
		});
		fetchMessages().then((res) => {
			// console.log('fetchMessages', res);
			const tarMsg = res.data.map((item: any) => item.message);
			// console.log('_.xor(tarMsg, msgs)', _.xor(tarMsg, msgs));
			// setMsgs([tarMsg, test]);
			if (_.xor(tarMsg, msgs).length) {
				setRawMsgs(res.data);
				setMsgs(tarMsg);
			}
		}).catch((e) => {
			// console.log('Error fetching messages');
			// const tarMsg = ['這不是', '真正的', '文案'];
			// // console.log('_.xor(tarMsg, msgs)', _.xor(tarMsg, msgs));
			// if (_.xor(tarMsg, msgs).length) {
			// 	setMsgs(tarMsg);
			// }
		}).then(() => {
			setRefreshTrigger(refreshTrigger + 1);
		});
		setTestCount(testCount + 1);
	};

	useEffect(() => {
		if ((msgs && msgs.length) && (questions && questions.length) && !groupedMsgs.length) {
			console.log('msgs', rawMsgs);
			const firstQuestion = questions[questions.length - 1];
			console.log('firstQuestion.Date', firstQuestion.Date);
			const newMsgsGroup = [
				{
					type: 'question',
					msg: '致香港 To Hong Kong',
				},
			];
			rawMsgs.forEach((msg: any) => {
				const msgMoment = moment(msg.createdAt);
				const firstQuestionMoment = moment(firstQuestion.Date, 'YYYY-MM-DD');
				if (msgMoment.isBefore(firstQuestionMoment)) {
					newMsgsGroup.push({
						type: 'answer',
						msg: msg.message,
					});
				}
			});
			questions.forEach((question: any, index: number) => {
				const questionMoment = moment(question.Date);
				const nextQuestion = (index + 1 < questions.length) ? questions[index + 1] : false;
				const nextQuestionMoment = (nextQuestion) ? moment(nextQuestion.Date) : moment('9999-12-31', 'YYYY-MM-DD');
				newMsgsGroup.push({
					type: 'question',
					msg: question.Question,
				});
				rawMsgs.forEach((msg: any) => {
					const msgMoment = moment(msg.createdAt);
					if (msgMoment.isSameOrAfter(questionMoment, 'day') && msgMoment.isBefore(nextQuestionMoment, 'day')) {
						newMsgsGroup.push({
							type: 'answer',
							msg: msg.message,
						});
					}
				});
			});
			console.log('newMsgsGroup.Date', newMsgsGroup);

			// questions.forEach((questions) => {

			// });
			setGroupedMsg(newMsgsGroup);

			// Last msg
		}
	}, [msgs, questions, rawMsgs]);

	useEffect(() => {
		console.log('groupedMsgs', groupedMsgs);
	}, [groupedMsgs]);
	useEffect(() => {
		// Work around for "/wall" path
		checkWallQuery();
		// On load trigger resize to prevent sandtable display issue
		setTimeout(() => {
			window.dispatchEvent(new Event('resize'));
		}, 1500);
		if (screenMode !== 'fullscreen') {
			setTimeout(() => {
				// setMode('uniform');
			}, 2500);
		}
		loadMsgs();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	const MsgArea = () => (
		<div
			className="sandtable-msg__msgs"
		>
			{
				groupedMsgs.map((groupedMsg: any, index: number) => (
					<React.Fragment key={groupedMsg._id}>
						{index > 0 && <span className="sandtable-msg__dash" />}
						{ groupedMsg.type === 'question' ? (
							<em>{groupedMsg.msg}</em>
						) : groupedMsg.msg}
					</React.Fragment>
				))
			}
			{/* <span dangerouslySetInnerHTML={{ __html: `<em>致香港 To Hong Kong</em> <span></span> ${msgs.join(' <span class="sandtable-msg__dash"></span> ')}` }} /> */}
			<LoaderDots
				show={isTyping}
			/>
		</div>
	);
	useInterval(() => {
		if (screenMode === 'fullscreen') {
			loadMsgs();
		}
	}, 40000);

	// Scroll to section
	const scrollToSec = (key: string) => {
		scroller.scrollTo(`anchor-${key}`, {
			duration: 1000,
			delay: 50,
			smooth: true,
			offset: -40,
		});
	};

	// Control Sandtable
	const addMsg = () => {
		const newMsg = inputMsg;
		const newMsgs = [
			...msgs,
			newMsg,
		];
		submitMessages(newMsg).then(() => {
			// console.log('submitted');
		});
		setMsgs(newMsgs);
		setGroupedMsg([
			...groupedMsgs,
			{
				type: 'answer',
				msg: newMsg,
			},
		]);
		setInputMsg('');
	};

	// Edit and remove msg

	// const [editTextMode, setEditTextMode] = useState<boolean>(false); // Edit Msg
	// const [editMsg, setEditMsg] = useState<string>('');
	// const [zoomValue, setZoomValue] = useState<number>(0);

	// useEffect(() => {
	// 	setEditMsg(msgs.join('___'));
	// }, [msgs]);

	// const toggleFullscreen = () => {
	// 	const targetMode = screenMode === 'halfscreen' ? 'fullscreen' : 'halfscreen';
	// 	setScreenMode(targetMode);
	// };
	// const saveEditedMsg = () => {
	// 	const editedMsg: string[] = editMsg.split('___');
	// 	setMsgs(editedMsg);
	// 	setEditTextMode(false);
	// };

	// const cancelEditedMsg = () => {
	// 	setEditMsg(msgs.join('___'));
	// 	setEditTextMode(false);
	// };

	const onFocusInput = () => {
		setIsTyping(true);
	};

	const onBlurInput = () => {
		setIsTyping(false);
	};
	const toSection = (targetSection: string) => {
		scrollToSec(targetSection);
		setSectionOpen(targetSection);
	};

	const SandTableSidebar = () => (
		<div className="sandtable__sidebar">
			<button
				type="button"
				onClick={() => toSection('about')}
				className="sandtable__sidelink"
			>
				About
			</button>
			<button
				type="button"
				onClick={() => toSection('acknowledgements')}
				className="sandtable__sidelink"
			>
				Acknowledgements
			</button>
			<button
				type="button"
				onClick={() => toSection('follow')}
				className="sandtable__sidelink"
			>
				Follow
			</button>
		</div>
	);

	return (
		<div
			className={`sandtable is-${screenMode}`}
		>
			<div className="sandtable__mobile-menu">
				<SandTableSidebar />
			</div>
			<div
				className="sandtable__body"
			>
				<div
					className="sandtable__board"
				>
					<div className="sandtable__head">
						<img className="sandtable__logo-en" src={`${window.PUBLIC_URL}/img/img-sandtable-title.svg`} alt="" />
						<img className="sandtable__logo-zh" src={`${window.PUBLIC_URL}/img/img-sandtable-title-zh.svg`} alt="" />
					</div>
					<div className="sandtable-board">
						<TransformWrapper
							defaultScale={1}
							// defaultPositionX={-960}
							// defaultPositionY={450}
							options={{
								minScale: 1,
								maxScale: 4,
							}}
							onZoomChange={(value: any) => {
								window.canvasZoom = ((value.scale - 1) / 3) * 100;
							}}
						>
							{({
								zoomIn, zoomOut, resetTransform, setScale, ...rest
							}: any) => (
								<>
									<TransformComponent>
										<div className="sandtable-board__zoomer">
											<div className="sandtable-board__body">
												<SandTest2
													chars={msgs}
													particlesBackToOrigin={particlesBackToOrigin}
													mode={mode}
													screenMode={screenMode}
													refreshTrigger={refreshTrigger}
												/>
											</div>
										</div>
									</TransformComponent>
									<div className="tools-slider">
										<div className="tools-slider__wrapper">
											<ReactSlider
												className="tools-slider__body"
												thumbClassName="tools-slider__thumb"
												trackClassName="tools-slider__track"
												renderThumb={(props, state) => <div {...props}>{state.valueNow}</div>}
												defaultValue={window?.canvasZoom || 0}
												value={window?.canvasZoom || 0}
												onChange={(val) => {
													const scale = 1 + 3 * (val / 100);
													setScale(scale);
												}}
											/>
										</div>
										<button className="tools-slider__zoom-out" type="button" title="Zoom Out" onClick={zoomOut}>-</button>
										<button className="tools-slider__zoom-in" type="button" title="Zoom In" onClick={zoomIn}>+</button>
									</div>
									{/*
										<div className="tools">
											<button type="button" title="Fullscreen" className="fullscreen-btn" onClick={() => toggleFullscreen()}>[ ]</button>
											<button type="button" title="Zoom In" onClick={zoomIn}>+</button>
											<button type="button" title="Zoom Out" onClick={zoomOut}>-</button>
											<button type="button" title="Reset Zoom" onClick={resetTransform}>x</button>
										</div>
									*/}
								</>
							)}
						</TransformWrapper>
					</div>
					<nav>
						<span className="ip-nav-left" />
						<span className="ip-nav-right" />
					</nav>
				</div>
				<div
					className="sandtable__control"
				>
					<div className="sandtable__head">
						<div />
						<div className="sandtable__sitetitle">
							<span>Write Together 一起書寫</span>
						</div>
					</div>
					<div
						className="sandtable-interact-panel"
					>
						<div className="sandtable-msg">
							<div className="sandtable-msg__title">
								一起書寫香港之書 Let’s continue the story of Hong Kong
								{questions && questions.length && (
									<>
										<br />
										{questions[0].Question}
									</>
								)}

							</div>
							<div className="sandtable-msg__body">
								<ScrollPlane startAtBottom>
									<MsgArea />
								</ScrollPlane>
							</div>
							<div className="sandtable-msg-input">
								<input
									type="text"
									value={inputMsg}
									placeholder="一起書寫 Write Together "
									onFocus={() => onFocusInput()}
									onBlur={() => onBlurInput()}
									onChange={(e) => setInputMsg(e.target.value)}
								/>
								<button
									type="button"
									onClick={() => addMsg()}
								>
									Submit
								</button>
							</div>
						</div>
						<About
							open={sectionOpen === 'about'}
							onClose={() => setSectionOpen('')}
						/>
						<Acknowledgements
							open={sectionOpen === 'acknowledgements'}
							onClose={() => setSectionOpen('')}
						/>
						<Follow
							open={sectionOpen === 'follow'}
							onClose={() => setSectionOpen('')}
						/>
					</div>
					<SandTableSidebar />
				</div>
			</div>
		</div>
	);
};

export default SandTable;
